<template lang="html">

  <div class="podaci">
    <h1>Ime: {{user.name}}</h1>
    <h4>email: {{user.email}}</h4>
  </div>

  <div class="accordion" id="accordionExample">
    <div class="accordion-item">
      <h2 class="accordion-header" id="headingOne">
        <button class="accordion-button" type="button" data-bs-toggle="collapse" data-bs-target="#collapseOne" aria-expanded="true" aria-controls="collapseOne">
          Performance Evaluation
        </button>
      </h2>
      <div id="collapseOne" class="accordion-collapse collapse show" aria-labelledby="headingOne" data-bs-parent="#accordionExample">
        <div class="accordion-body">
          <div class="evaluation1" v-if="((randomNumber/100).toFixed(2)<0.75)">
              <h4>Rezultat: {{(randomNumber/100).toFixed(2)}}</h4>
              <p>Rezultati loši, potrebno unapređenje za vršenje trenutne uloge ili prelazak na drugo radno mesto	</p>
          </div>

          <div class="evaluation2" v-if="((randomNumber/100).toFixed(2)>=0.75 && (randomNumber/100).toFixed(2)<=0.99 )">
              <h4>Rezultat: {{(randomNumber/100).toFixed(2)}}</h4>
              <p>Rezultati dobri, ali  neophodan razvoj za vršenje trenutne uloge</p>
          </div>

          <div class="evaluation3" v-if="((randomNumber/100).toFixed(2)>0.99 && (randomNumber/100).toFixed(2)<=1.17 )">
              <h4>Rezultat: {{(randomNumber/100).toFixed(2)}}</h4>
              <p>Apsolutno ostvarenje rezultata u trenutnoj ulozi</p>
          </div>

          <div class="evaluation3" v-if="((randomNumber/100).toFixed(2)>1.17)">
              <h4>Rezultat: {{(randomNumber/100).toFixed(2)}}</h4>
              <p>Prebačaj rezultata u trenutnoj ulozi</p>
          </div>
        </div>
      </div>
    </div>
    <div class="accordion-item">
      <h2 class="accordion-header" id="headingTwo">
        <button class="accordion-button collapsed" type="button" data-bs-toggle="collapse" data-bs-target="#collapseTwo" aria-expanded="false" aria-controls="collapseTwo">
          The Vitality Quotient
        </button>
      </h2>
      <div id="collapseTwo" class="accordion-collapse collapse" aria-labelledby="headingTwo" data-bs-parent="#accordionExample">
        <div class="accordion-body">

            <div id="vqTest">
            <h4>VQ Test</h4>
            <p v-if="!user.vqTest.finished">Nije uradjen</p>
            <div class="" v-if="user.vqTest.finished">
              <p>Radjen: {{vqDate}}</p>
              <div class="vqPovucen" v-if="this.user.vqTest.result<=-15">
                <h3>"KV" prоfil је: "Pоvučеn". ({{this.user.vqTest.result}})</h3>
                <p>Vаšа "vоlја zа živоtоm"  је slаbа ili pоtisnutа. Vi nistе zаdоvоlјni svеtоm ili živоtоm tаkvim kаkvi jesu, аli nistе sklоni dа ih menjate. Gеnеrаlnо sе оsеćаtе ugrоžеnim, dоgаđајimа i lјudimа. Višе vоlitе dа sе pоvučеtе nеgо dа se eksponirate. Žеlitе dа vоditе mirаn živоt, dаlеkо оd svеtskоg mеtеžа. Nеmаtе ni јаkе žеlје, ni јаkе аmbiciје. Drugi Vаs mоgu dоživеti kао pаsivnе ili stаtičnе. Bеz iniciјаtivе, nеprеduzimlјivе. Kао nеkоg kо višе vоli kоntеmplаciјu оd аkciје. Vеrоvаtnо је Vаš mоtо: "Nеmа pоtrеbе gurаti rеku, оnа svаkаkо tеčе."</p>

              </div>
              <div class="vqOprezan"  v-if="(this.user.vqTest.result>-15)&&(this.user.vqTest.result<=-1)">
                <h3>"VK" Profil je: "Oprezan." ({{this.user.vqTest.result}})</h3>
                <p>Vasa "volja za zivotom" je u pripravnosti. Vi ste budni, posmatrate svet oko sebe i procenjujete potencijalni uticaj dogadjaja na Vas zivot. Spremni ste da reagujete ako je potrebno, ali niste skloni da se pokrenete i preuzmete inicijativu, ukoliko niste primorani na to. </p>
                <p>Pоkušаćete dа izbеgnе rizikе i dа sе zаštititе оd nеоčеkivаnоg. Pоuzdаni ste kао prоfеsiоnаlаc i kао priјаtеlј, svе dоk sе nе nаđеtе u kоntrоvеrznој ili rizičnој situаciјi. Prihvаtićеtе оpšti trеnd rаzmišlјаnjа zа kојi prоcеnitе dа је rаzumаn. U principu vоlitе lјudе, аli žеlitе dа prоvеritе dа li su pоuzdаni. Kаdа sе sа nеkim priјаtnо оsеćаtе, dаćеtе svе оd sеbе dа mu budеtе оd pоmоći. Drugi Vаs dоživlјаvајu prе kао nеkоg kо prihvаtа inciјаtivu, nеgо kао iniciјаtоrа. Kао nеkоg kо ćе sеdеti nа оgrаdi dоk nе vidi u kоm prаvcu vеtаr duvа. Kао stеrеоtipnоg pо nаčinu rаzmišlјаnjа i kао nе bаš stimulаtivnоg. Vаš mоtо је vеrоvаtnо: "Višе vоlim dа igrаm nа sigurnо".</p>

              </div>
              <div class="vqAngazovan" v-if="(this.user.vqTest.result>=1)&&(this.user.vqTest.result<=14)">
                <h3>"VK" Profil je:"Angažovan". ({{this.user.vqTest.result}})</h3>
                <p>Vаšа "vоlја zа živоtоm" је аktivirаnа. Vi stе "аktеr". Pоkrеćе Vаs svеt. Živоt је pun mоgućnоsti kоје su i zаbаvnе i kоrisnе. Žеlitе dа idеtе dаlје: dа rаditе stvаri i dа uživаtе u živоtu. Vi ćеtе inicirаti nоvе prојеktе, kаkо u Vаšеm prоfеsiоnаlnоm, tаkо i u Vаšеm ličnоm živоtu. Privućićе Vаs lјudi sа kојimа оsеćаte dа mоžеtе dа rаditе rаznе stvаri: pаrtnеri, prе nеgо drugоvi ili srоdnе dušе. Vоlitе prеuzimаnjе оdgоvоrnоsti i nе plаšitе sе оd pојаvlјivаnjа u јаvnоsti i prihvatanja rizikа. Маštоviti stе.</p>
                <p>Drugi lјudi ćе Vаs vеrоvаtnо dоživеti kао lidеrа, kао nеkоg sа idејаmа i iniciјаtivоm, аktivnоg i pоuzdаnоg, zаnimlјivоg. Моždа ćе Vаs dоživеti i kао prеvišе zаuzеtоg, kао nеkоg kо sе nе prеdаје zаbаvi ili оpuštаnju, kао nеkоg kоgа sе nе tiču ličnа оsеćаnjа ili prоblеmi drugih lјudi, kао еgоistu.</p>
                <p>Vаš mоtо је vеrоvаtnо:"Hајdе dа tо urаdimо!"</p>
              </div>
              <div class="vqHiperaktivan" v-if="this.user.vqTest.result>=15" >
                <h3>"KV" prоfil је: "Hipеr-аktivаn". ({{this.user.vqTest.result}})</h3>
                <p>Vаšа "vоlја zа živоtоm" је оgrоmnа. Vi stе "prеduzеtnik", "оsvајаč". U sebi kоnstаntnо osećate pritisаk. Vi sе nikаdа nе mоžеtе stvаrnо оpustiti i čаk i kаdа uživаtе u zаbаvi, uvеk i dаlје rаzmišlјаtе о nеkоm pоslu kојi trеbа dа оbаvitе. Моrаtе biti аngаžоvаni u nеkоm prојеktu i morate imati osećaj dа  stаlnо širitе svојu "mоć zа živоt". Pо Vаmа, "svеt trеbа zgrаbiti!" Vi stе nеstrplјivi, nikаd zаdоvоlјni, оsim tоkоm krаtkih trеnutаkа pоstignućа. Pоtrеbnо Vаm је dа vоditе, dа prеuzimаtе оdgоvоrnоst, јеr u suprоtnоm mislitе dа sе stvаri nеćе оdviјаti brzо ili dоvоlјnо еfikаsnо, оdnоsnо nеćе ići "nа Vаš nаčin". Drugi lјudi su u suštini izvršiоci Vаših pоduhvаtа, oni trеbа dа iznеsu Vаšе prојеktе. Nistе uоpštе zаintеrеsоvаni zа njihоv lični živоt. Vеоmа stе krеаtivni. Оd Vаs mоžе pоstаti vеliki lidеr, pоd uslоvоm dа nе prеtеruјеtе ...</p>
                <p>Drugi Vаs uglаvnоm vidе kао hipеr-аktivаnоg, vrlо mаštоvitоg, uvеk u pоkrеtu, inоvаtivnоg. Sа mаnjе pоzitivnе strаnе, mоgli bi Vаs smаtrаti zа prеvišе nаpаdnоg, аrоgаntnоg, аgrеsivnоg, nаpоrnоg, zаpоvеdnički nаstrојеnоg. Kао nikаd zаdоvоlјnоg rаdоhоličаrа, kојi nе shvаtа u čеmu su prаvе vrеdnоsti živоtа. Kао nеоsеtlјivоg prеmа drugim lјudimа i bеz ličnоg živоtа.</p>
                <p>Vаš mоtо је vеrоvаtnо: "Višе је bоlје".</p>
              </div>
            </div>
            <img src="@/assets/goldWApp.svg" alt="">

            </div>
            <button @click="makePDFVQ">MakePDF</button>
        </div>
      </div>
    </div>
    <div class="accordion-item">
      <h2 class="accordion-header" id="headingThree">
        <button class="accordion-button collapsed" type="button" data-bs-toggle="collapse" data-bs-target="#collapseThree" aria-expanded="false" aria-controls="collapseThree">
          Test Licnosti
        </button>
      </h2>
      <div id="collapseThree" class="accordion-collapse collapse" aria-labelledby="headingThree" data-bs-parent="#accordionExample">
        <div class="accordion-body">
        <p v-if="!user.testLicnosti.finished">Nije uradjen</p>
        <div class="" v-if="user.testLicnosti.finished">
          <p>Radjen: {{testLicnostiDate}}</p>
          <line-chart :data="chartData"  :library="chartOptions" id="chart" width="800px" height="300px" v-if="!isMobile()" :download="true"> </line-chart>
          <line-chart :data="chartDataDetails" :library="chartOptionsDetailed" id="detailedChart" :download="true" width="800px" height="300px" v-if="!isMobile()"> </line-chart>
        </div>
        </div>
      </div>
    </div>
    <div class="accordion-item">
      <h2 class="accordion-header" id="headingFour">
        <button class="accordion-button collapsed" type="button" data-bs-toggle="collapse" data-bs-target="#collapseFour" aria-expanded="false" aria-controls="collapseFour">
          Belbinov Test
        </button>
      </h2>
      <div id="collapseFour" class="accordion-collapse collapse" aria-labelledby="headingFour" data-bs-parent="#accordionExample">
        <div class="accordion-body">
          <div id="belvin">
              <table id="belvinTable">
                <tr>
                  <th>Uloge</th>
                  <th>Rezultati</th>
                </tr>
                <tr>
                  <td>Snalažljivi Organizator</td>
                  <td v-if="user.belbinTest.SO <= 6">Nisko ({{user.belbinTest.SO}})</td>
                  <td v-if="(user.belbinTest.SO > 6) && (user.belbinTest.SO<=11)">Prosecno ({{user.belbinTest.SO}})</td>
                  <td v-if="(user.belbinTest.SO > 11) && (user.belbinTest.SO<=16)">Visoko ({{user.belbinTest.SO}})</td>
                  <td v-if="user.belbinTest.SO >= 17">Veoma Visoko ({{user.belbinTest.SO}})</td>
                </tr>
                <tr>
                  <td>Rođeni Vođa</td>
                  <td v-if="user.belbinTest.RV <= 6">Nisko ({{user.belbinTest.RV}})</td>
                  <td v-if="(user.belbinTest.RV > 6) && (user.belbinTest.RV<=10)">Prosecno ({{user.belbinTest.RV}})</td>
                  <td v-if="(user.belbinTest.RV > 10) && (user.belbinTest.RV<=13)">Visoko ({{user.belbinTest.RV}})</td>
                  <td v-if="user.belbinTest.RV >= 14">Veoma Visoko ({{user.belbinTest.RV}})</td>
                </tr>
                <tr>
                  <td>Osoba od Akcije</td>
                  <td v-if="user.belbinTest.OA <= 8">Nisko ({{user.belbinTest.OA}})</td>
                  <td v-if="(user.belbinTest.OA > 8) && (user.belbinTest.OA<=13)">Prosecno ({{user.belbinTest.OA}})</td>
                  <td v-if="(user.belbinTest.OA > 13) && (user.belbinTest.OA<=17)">Visoko ({{user.belbinTest.OA}})</td>
                  <td v-if="user.belbinTest.OA >= 18">Veoma Visoko ({{user.belbinTest.OA}})</td>
                </tr>
                <tr>
                  <td>Osoba sa Idejama</td>
                  <td v-if="user.belbinTest.OI <= 4">Nisko ({{user.belbinTest.OI}})</td>
                  <td v-if="(user.belbinTest.OI > 4) && (user.belbinTest.OI<=8)">Prosecno ({{user.belbinTest.OI}})</td>
                  <td v-if="(user.belbinTest.OI > 8) && (user.belbinTest.OI<=12)">Visoko ({{user.belbinTest.OI}})</td>
                  <td v-if="user.belbinTest.OI >= 13">Veoma Visoko ({{user.belbinTest.OI}})</td>
                </tr>
                <tr>
                  <td>Osoba za Kontakte</td>
                  <td v-if="user.belbinTest.OK <= 6">Nisko ({{user.belbinTest.OK}})</td>
                  <td v-if="(user.belbinTest.OK > 6) && (user.belbinTest.OK<=9)">Prosecno ({{user.belbinTest.OK}})</td>
                  <td v-if="(user.belbinTest.OK > 9) && (user.belbinTest.OK<=11)">Visoko ({{user.belbinTest.OK}})</td>
                  <td v-if="user.belbinTest.OK >= 12">Veoma Visoko ({{user.belbinTest.OK}})</td>
                </tr>
                <tr>
                  <td>Sudija</td>
                  <td v-if="user.belbinTest.S <= 5">Nisko ({{user.belbinTest.S}})</td>
                  <td v-if="(user.belbinTest.S > 5) && (user.belbinTest.S<=9)">Prosecno ({{user.belbinTest.S}})</td>
                  <td v-if="(user.belbinTest.S > 9) && (user.belbinTest.S<=12)">Visoko ({{user.belbinTest.S}})</td>
                  <td v-if="user.belbinTest.S >= 13">Veoma Visoko ({{user.belbinTest.S}})</td>
                </tr>
                <tr>
                  <td>Čovek Tima</td>
                  <td v-if="user.belbinTest.CT <= 8">Nisko ({{user.belbinTest.CT}})</td>
                  <td v-if="(user.belbinTest.CT > 8) && (user.belbinTest.CT<=12)">Prosecno ({{user.belbinTest.CT}})</td>
                  <td v-if="(user.belbinTest.CT > 12) && (user.belbinTest.CT<=16)">Visoko ({{user.belbinTest.CT}})</td>
                  <td v-if="user.belbinTest.CT >= 17">Veoma Visoko ({{user.belbinTest.CT}})</td>
                </tr>
                <tr>
                  <td>Perfekcionista</td>
                  <td v-if="user.belbinTest.P <= 3">Nisko ({{user.belbinTest.P}})</td>
                  <td v-if="(user.belbinTest.P > 3) && (user.belbinTest.P<=6)">Prosecno ({{user.belbinTest.P}})</td>
                  <td v-if="(user.belbinTest.P > 6) && (user.belbinTest.P<=9)">Visoko ({{user.belbinTest.P}})</td>
                  <td v-if="user.belbinTest.P >= 10">Veoma Visoko ({{user.belbinTest.P}})</td>
                </tr>
              </table>
              <div v-if="belbinNajvisiPrvi==0">
                <p><b>Snalažljivi Organizator ({{user.belbinTest.SO}})</b> Koncepte i planove pretvara u praktičnu akciju. Planove ostvaruje sistematično i efektno. Odlike: uravnotežen i disciplinovan. Zahvaljujući njemu projekti i planovi se i praktično primenjuju. Voli tačnost i doslednost, ne voli kada se planovi menjaju.</p>
              </div>
              <div v-if="belbinNajvisiPrvi==1">
                <p><b>Rođeni Vođa ({{user.belbinTest.RV}})</b> Daje svoj pečat i kontroliše način na koji grupa pokušava da ostvari svoje ciljeve. Sposoban je da uspešno iskoristi kvalitete koje grupa poseduje. Zna koje su jake, a koje su slabe tačke članova grupe i u stanju je da iskoristi prednosti i potencijale svakog pojedinca u grupi. Odlike: uravnotežen, dominantan, ekstrovertan. Više se oslanja na zdravorazumsko rasuđivanje nego na intelektualno promišljanje. Nije agresivan dok upravlja grupom.</p>
              </div>
              <div v-if="belbinNajvisiPrvi==2">
                <p><b>Osoba Od Akcije ({{user.belbinTest.OA}})</b> Oblikuje način na koji se koriste potencijali grupe. Drži pažnju usmerenu na određivanje ciljeva i prioriteta, vodi grupnu diskusiju i oblikuje rezultate grupnih aktivnosti. Odlike: neumoran, dominantan, ekstrovertan, impulsivan, lako se iznervira. Želi brzo da dođe do rezultata. Poseduje takmičarski duh, arogantan je, ali zahvaljujući njemu zadaci bivaju obavljeni.</p>
              </div>
              <div v-if="belbinNajvisiPrvi==3">
                <p><b>Osoba Sa Idejama ({{user.belbinTest.OI}})</b> Predlaže nove ideje i strategije, pojedinačno obrađuje najvažnije probleme, pokušava da nametne svoje sopstvene ideje, koristeći grupni pristup problemu (na bazi konfrontacije). Odlike: dominantan, inteligentan, introvertan. Može se desiti da se izgubi u detaljima. Takođe kritikuje tuđe ideje. Što je problem veći, to je za njega veći i izazov da ga reši. Smatra da ideje mogu zvučati čudno ili nemoguće (u početku). Oko sebe stvara auru genija. </p>
              </div>
              <div v-if="belbinNajvisiPrvi==4">
                <p><b>Osoba Za Kontakte ({{user.belbinTest.OK}})</b> Istražuje, analizira i sabira informacije o idejama, dostignućima i delatnostima van grupe. Ostvaruje veze sa ljudima, koje mogu biti korisne za grupu, sposoban je da vodi pregovore. Odlike: uravnotežen, dominantan, ekstrovertan. Podstiče inovacije, dobar improvizator. Pomalo suzdržan kada traži dobrobit za grupu. Često kaže: „Nove mogućnosti biće stvorene na tuđim greškama“.</p>
              </div>
              <div v-if="belbinNajvisiPrvi==5">
                <p><b>Sudija ({{user.belbinTest.S}})</b> Analizira problem, procenjuje ideje i predloge. Zahvaljujući njemu grupa ne prenagljuje i donosi dobro promišljene odluke. Odlike: inteligentan, uravnotežen, introvertan. Najobjektivniji je, celovita ličnost, ne unosi emocije, voli da ima vremena za razmišljanje. Ponekad mu manjka entuzijazma, ali njegova smirenost doprinosi da grupa donosi odmerene odluke. </p>
              </div>
              <div v-if="belbinNajvisiPrvi==6">
                <p><b>Čovek Tima ({{user.belbinTest.CT}})</b> Podržava članove grupe, u slučaju greške uvek održava visok moral grupe, sposoban je da izbegne konflikt, stvara timski duh, osnažuje saradnju i komunikaciju u grupi. Odan je grupi. Odlike: ekstrovertan, uravnotežen, nema veliku potrebu za dominacijom i rivalstvom, saosećajan. Njegov doprinos možda nije baš sasvim jasan, ali njegova odanost i posvećenost timu su na velikoj ceni. Ne voli da se konfrotira.</p>
              </div>
              <div v-if="belbinNajvisiPrvi==7">
                <p><b>Perfekcionista ({{user.belbinTest.P}})</b> Usmeren je na specifične akcije: privođenje zadataka kraju u okviru određenog roka i obezbeđivanje razultata najvišeg kvaliteta. Može biti težak u kontaktima, zbog toga što se gubi u detaljima koji nisu toliko važni za izvođenje zadatka. Uvek je svestan ciljeva. Odlike: težak, napet, introvertan, disciplinovan.</p>
              </div>


              <div v-if="belbinNajvisiDrugi==0">
                <p><b>Snalažljivi Organizator ({{user.belbinTest.SO}})</b> Koncepte i planove pretvara u praktičnu akciju. Planove ostvaruje sistematično i efektno. Odlike: uravnotežen i disciplinovan. Zahvaljujući njemu projekti i planovi se i praktično primenjuju. Voli tačnost i doslednost, ne voli kada se planovi menjaju.</p>
              </div>
              <div v-if="belbinNajvisiDrugi==1">
                <p><b>Rođeni Vođa ({{user.belbinTest.RV}})</b> Daje svoj pečat i kontroliše način na koji grupa pokušava da ostvari svoje ciljeve. Sposoban je da uspešno iskoristi kvalitete koje grupa poseduje. Zna koje su jake, a koje su slabe tačke članova grupe i u stanju je da iskoristi prednosti i potencijale svakog pojedinca u grupi. Odlike: uravnotežen, dominantan, ekstrovertan. Više se oslanja na zdravorazumsko rasuđivanje nego na intelektualno promišljanje. Nije agresivan dok upravlja grupom.</p>
              </div>
              <div v-if="belbinNajvisiDrugi==2">
                <p><b>Osoba Od Akcije ({{user.belbinTest.OA}})</b> Oblikuje način na koji se koriste potencijali grupe. Drži pažnju usmerenu na određivanje ciljeva i prioriteta, vodi grupnu diskusiju i oblikuje rezultate grupnih aktivnosti. Odlike: neumoran, dominantan, ekstrovertan, impulsivan, lako se iznervira. Želi brzo da dođe do rezultata. Poseduje takmičarski duh, arogantan je, ali zahvaljujući njemu zadaci bivaju obavljeni.</p>
              </div>
              <div v-if="belbinNajvisiDrugi==3">
